.container {
    position: absolute;
    top: 20vh;
    left: 20vw;
}
.container h3 {
    font-size: smaller;
}

.direction {
    /* transition: all .01s; */
    background-color: white;

    width: 200px;
    height: 200px;
    display: flex;
    justify-content: center;
    border-radius: 50%;
    /* align-items: center; */
}

.north-rotate {
    /* transform: rotate(230deg); */
}
.arrow {
    position: relative;
    top:-28px;
    /* background-color: red; */
    width: 0;
    height: 0;
    border-left: 20px solid transparent;
    border-right: 20px solid transparent;
    
    border-bottom: 30px solid rgb(255, 0, 0);
    /* transform: rotate(-90deg); */

}

.client-direction {
    width:60% ;
    height: 60%;
    align-self: center;
    background-color: aquamarine;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    
}

.client-arrow {
    position: relative;
    top:-25px;
    /* background-color: red; */
    width: 0;
    height: 0;
    border-left: 15px solid transparent;
    border-right: 15px solid transparent;
    
    border-bottom: 22px solid rgb(0, 47, 255);

}